.scroll-button {
  cursor: pointer;
  width: 50px;
  height: 50px;
  color: #9bb537;
  z-index: 10000;
  background-color: #9bb537;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  transition: transform .3s;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.scroll-button:hover, .scroll-button:focus {
  background-color: #9bb537;
  transform: scale(.95);
  box-shadow: 0 10px 20px 5px rgba(0, 0, 0, .5);
}

.scroll-button:before, .scroll-button:after {
  content: "";
  width: 20px;
  height: 3px;
  background-color: #fff;
  position: absolute;
}

.scroll-button:before {
  top: 45%;
  right: 16%;
  transform: rotate(230deg);
}

.scroll-button:after {
  top: 45%;
  left: 20%;
  transform: rotate(-230deg);
}

.scroll-button:focus {
  outline: none;
}

